import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles({

    card: {
        minHeight: '45vh'
    },
    center: {
        textAlign: 'center',
    },
    cardContent: {
        textAlign: 'end',
    },
});
export default function ProjectCard({ ministry, project, page, program, TotalProjectMoney }) {
    const classes = useStyles();

    return (
        <Card className={classes.card}>
            <CardContent >
                <p dir='auto'>
                    <strong>  المجال :</strong> {ministry}
                </p>

                <Typography className={classes.center} variant="h5" component="h3" color="textPrimary">
                    <strong> {project}</strong>
                </Typography>

                <p dir='auto'>
                    <strong> البرنامج الوطني :</strong> {program}

                </p>

                <p dir='auto'>
                    <strong>الميزانية الجملية : </strong> {TotalProjectMoney}
                </p>

                <p dir='auto'>
                    <strong>  الصفحة :</strong> {page}
                </p>

            </CardContent>

        </Card>

    )
}